/******************************************************************
HEADER STYLES
Styles specific to the site header
******************************************************************/

.is-mobile-menu-open {
    @extend .has-no-scroll-y;
}

.header-offset {
    height: 130px;
    font-size: 0;

    @include below-mobile {
        height: 90px;
    }
}

.header {
    position: fixed;
    top: 0;
    left: 0; right: 0;
    background: $color-white;
    box-shadow: $shadow02;
    z-index: 10;
    opacity: 1;
    transition: all $trans-time-md $bezier01;

    &__container {
        padding: 0 0 0 40px;
    }

    &__inner {
        display: flex;
        position: relative;
        padding: 16px 0;
        min-height: 130px;
        transition: all $trans-time-md $bezier01;
    }

    &__main {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__content {
        display: flex;
        align-items: center;
    }

    &__logo {
        margin: 0 50px 0 0;
    }

    &__languages {
        margin: 0 50px 0 0;
    }

    &__account {
        margin: 0 -8px 0 56px;
        padding-left: 6px;
        //overflow: hidden;
    }

    .header__account-button {
        padding-right: 48px;
    }

    &__socials {
        margin: 0 0 0 24px;
    }

    &__toggle {
        display: none;
        align-items: center;
    }

    &__cart {
        position: absolute;
        bottom: -92px;
        right: 40px;
    }

    @include above-desktop-lg {
        &.is-up {
            opacity: 0;
            top: -20px;
            transition: all $trans-time-md $bezier01;
            pointer-events: none;
        }
    }

    @include below-desktop-xl {
        &__account {
            margin-left: 25px;
        }

        &__logo {
            margin-right: 25px;
        }
    }

    @include below-desktop-lg {
        &__container {
            padding: 0 40px;
        }

        &__toggle {
            display: flex;
            margin-right: -11px;
        }

        &__languages {
            display: none;
        }

        &__content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: fixed;
            left: 0; right: 0;
            top: 0; bottom: 0;
            width: 100%;
            height: 100vh;
            padding: 160px 0 40px 0;
            background: $color-white;
            text-align: center;
            overflow-x: auto;

            visibility: hidden;
            opacity: 0;
            transform: translateY(-8px);
            transition: $transition-default;
        }

        &__account, &__socials {
            margin: 40px 0 0 0;
        }

        &__cart {
            display: flex;
            align-items: center;
            position: relative;
            bottom: initial; right: initial;
            margin-right: 16px;
        }
    }

    @include below-mobile {
        &__container {
            padding: 0 20px;
        }

        &__inner {
            padding: 0;
            min-height: 90px;
        }

        &__content {
            padding: 100px 0 20px 0;
        }
    }
}

.header-nav {
    .menu {
        display: flex;
        flex-wrap: wrap;

        &__item {
            position: relative;
            padding: 0 5px;
            margin: 0 20px;
        }

        &__anchor {
            font-size: $font-size-sm;
            font-weight: $font-weight-bold;
            font-style: italic;
            text-decoration: none;
            text-transform: uppercase;

            @include above-desktop-lg {
                &:after {
                    content: "";
                    position: absolute;
                    bottom: -4px;
                    left: 0; right: 0;
                    width: 0%;
                    height: 4px;
                    background: $color01;
                    opacity: 0;
                    transition: $transition-default;
                }

                &:hover, &.is-active {
                    &:after {
                        width: 100%;
                        opacity: 1;
                        transition: $transition-default;
                    }
                }
            }
        }
    }

    @include below-desktop-xl {
        .menu {
            &__item {
                margin: 0 10px;
            }

            &__anchor {
                font-size: 15px;
            }
        }
    }

    @include below-desktop-lg {
        .menu {
            flex-direction: column;

            &__item {
                padding: 15px;
            }

            &__anchor {
                font-size: $font-size-md;
            }
        }
    }

    @include below-mobile-xs {
        .menu {
            &__anchor {
                font-size: $font-size-base;
            }
        }
    }
}

.header-logo {
    display: flex;
    align-items: center;
    z-index: 101;

    &__anchor {
        display: block;
        height: 45px;
        opacity: 1;
        transition: $transition-default;

        &:hover {
            opacity: $opacity-md;
            transition: $transition-default;
        }
    }

    &__img {
        display: block;
        height: 100%;
        max-width: initial;
    }

    @include below-mobile {
        &__anchor {
            height: 38px;
        }
    }

    @include below-mobile-xs {
        &__anchor {
            height: 32px;
        }
    }
}

.mobile-menu-toggle {
    position: relative;
    padding: 12px;
    height: 60px;
    width: 60px;
    font-size: 0;
    z-index: 101;

    &__icon {
        width: 36px;
        padding: 0;
        transition: all $trans-time $bezier01;

        &:before, &:after {
            content: '';
            background-color: $color-text;
            display: block;
            height: 4px;
            transition: $transition-default;
        }

        &:before {
            box-shadow: 0 10px 0 $color-text;
            margin-bottom: 16px;
        }
    }
}


// HEADER STATES
.is-scrolled {
    .header {
        &__inner {
            padding: 0;
            min-height: 90px;
        }
    }
}

.is-mobile-menu-open {
    @include below-desktop-lg {
        overflow-y: hidden;

        .header {
            &__content {
                visibility: visible;
                opacity: 1;
                transform: translateY(0);
                transition: $transition-default;
            }
        }

        .mobile-menu-toggle {
            &__icon {
                position: absolute;
                left: 11px;
                bottom: 15px;

                &:before {
                    box-shadow: 0 0 0 rgba($color01, 0);
                    transform: translateY(8px) rotate(45deg);
                }
                &:after {
                    width: 100%;
                    transform: translateY(-12px) rotate(-45deg);
                }
            }
        }
    }
}





.search-icon {
    background-image: url(https://demo.logic.ee/fitbody/wp-content/themes/fitbody/assets/icons/search-thick.svg);
    width: 24px;
    height: 23px;
    margin-right: 10px;;
    display: inline-block;
    background-size: contain; /* set the size of the icon */
    background-repeat: no-repeat; /* prevent the icon from repeating */
    z-index: 11;
}

.search-bar-container {
    display: none;
    position: absolute;
    top: 80px;
    right: 0;
    padding-bottom: 80px;
    padding-top: 25px;
    bottom: -40px;
    left: -40px;
    width: -webkit-fill-available;
    background: white;
}


.search-bar-container input[type="submit"] {
    cursor: pointer;
    margin-left: 10px;
    margin-right: -20px;
    margin-bottom: 23px;
    font-size: 18px;
    font-weight: 700;
    font-style: italic;
    text-decoration: none;
    text-transform: uppercase;
    background: #bda171;
    border: 0;
    width: 70px;
}

.search-bar-container form {
    display: inline-flex;
    width: -webkit-fill-available;
    height: 65px;
    flex-wrap: wrap;
    justify-content: center;
    transform: skew(-11deg);
   
}

.search-bar-container input[type="text"] {
    width: 540px;
    height: 60px;
    font-size: 18px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    border: 1px solid #bda171;
}

.search-bar-container INPUT[TYPE="text"]:focus {
    border: 1px solid #bda171;
}

.alert-message.is-active {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: row;
    margin-top: 120px;
    font-size: 22px;
    font-weight: 700;
    font-style: italic;
    text-decoration: none;
    text-transform: uppercase;
}


@media (max-width: 960px) {
    .search-bar-container input[type="text"] {
        height: 73px;
        width: 120px;
        font-size: 18px;
        font-weight: 700;
        font-style: normal;
        text-decoration: none;
        text-transform: uppercase;
        border: 0;
    }}

    @media (max-width: 960px) {
    .search-icon {
        margin-bottom: 20px;
    }}

    @media (max-width: 960px) {
        .search-bar-container input[type="submit"] {
            font-size: 18px;
            font-weight: 700;
            font-style: normal;
            text-decoration: none;
            text-transform: uppercase;
            border: 0;
            width: 80px;
        }}

        @media (max-width: 960px) {
            .search-bar-container form {
            display: inline-flex;
            transform: skew(-11deg);
        } }


        .header__socials {
            margin: 0 0 0 0px;
            display: none;
        }

        nav.header__nav.header-nav {
            z-index: 11;
        }

        .search-bar-container input.submit-btn:hover {
            color: #000;
            background: #E8D8BD;
            transition: all .24s ease;
        }