/******************************************************************
MODAL STYLES
Styles for pop-up modal components
******************************************************************/

.modal {
    display: none;
    //display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0; right: 0;
    top: 0; bottom: 0;
    width: 100%;
    height: 100%;
    padding: 20px;
    background: rgba($color-white, 0.9);
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 10;

    &__inner {
        display: block;
        margin: auto;
        position: relative;
        overflow-x: hidden;
    }

    &__close {
        position: absolute;
        top: 0; right: 0;
        width: 60px;
        height: 60px;
        padding: 20px;
        margin: 0;
        transition: $transition-default;

        &:hover {
            opacity: 0.6;
            transition: $transition-default;
        }
    }

    &__icon {
        display: block;
        width: 100%;
        height: 100%;
    }

    &.is-open {
        display: flex;
    }
}

.video-modal {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    padding: 40px;

    &__inner {
        box-shadow: $shadow02;  
    }
}