.language-switcher {
    position: relative;
    padding-left: 36px;

    &:before {
        content: "";
        position: absolute;
        left: 0; top: 0;
        width: 1px; //6px;
        height: 45px;
        background: $color01-light;
        transform: skew(-11deg);
    }

    &__items {
        display: flex;
        align-items: center;
        margin: 0 -12px;
        height: 45px;
    }

    &__item {
        padding: 0 12px;
        text-transform: uppercase;
        font-size: $font-size-sm;
        font-weight: $font-weight-bold;
    }

    @include below-desktop-xl {
        padding-left: 25px;

        &__items {
            margin: 0 -8px;
        }

        &__item {
            padding: 0 8px;
        }
    }
}