/******************************************************************
ITEM STYLES
Styles for granular items (mainly part of lists)
******************************************************************/

.blog-card {
    display: block;

    &:hover {
        color: $color-text;

        .blog-card__more {
            color: $color01;
            transition: $transition-default;

            .icon {
                animation-name: buttonIconBounceRight;
                animation-duration: $trans-time-md;
                animation-direction: forwards;
            }
        }
    }

    &__inner {
        display: flex;
        background: $color-white;
        padding: 14px;
        transition: $transition-default;
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex: 1;
        width: 100%;
        min-height: 400px;
        padding: 40px 60px 60px 100px;
    }

    &__main {

    }

    &__image {
        //flex: 0 0 500px;
        flex: 1;
        max-width: 500px;
        overflow: hidden;
        background-size: cover;
        background-position: 50% 50%;
        background-repeat: no-repeat;
    }

    /*&__img {
        display: block;
        height: 100%;
        width: auto;
    }*/

    &__date {
        display: block;
        font-style: italic;
        margin-bottom: 24px;
        line-height: 1;
    }

    &__title {
        margin: 0 0 24px 0;
        transition: $transition-default;
    }

    &__excerpt {
        margin: 0 0 48px 0;
    }

    &__more {
        font-style: italic;
        font-weight: $font-weight-bold;
        font-size: $font-size-h4;
        transition: $transition-default;
    }

    @include above-mobile {     
        &:hover {
            .blog-card__inner {
                transform: translateX(20px);
                transition: $transition-default;
            }
        }
    }

    @include below-desktop-lg {
        &__image {
            max-width: 420px;
        }

        &__content {
            padding: 40px 40px 60px 40px;
        }
    }

    @include below-desktop {
        &__excerpt, &__date {
            font-size: $font-size-sm;
        }

        &__title {
            font-size: 28px;
        }

        &__more {
            font-size: $font-size-base;
        }
    }

    @include below-mobile {
        &__inner {
            flex-direction: column-reverse;
        }

        &__image {
            max-width: 100%;
            min-height: 320px;
        }

        &__content {
            min-height: 120px;
            padding: 40px;
        }
    }

    @include below-mobile-sm {
        &__content {
            padding: 40px 20px;
        }

        &__image {
            min-height: 240px;
        }
    }
}

.testimonial-card {
    &__inner {
        padding: 0 40px;
    }

    &__quote {
        margin: 0;
        position: relative;
        font-size: $font-size-md;
        text-align: center;
        padding-top: 64px;

        &:before {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0; right: 0;
            width: 100%;
            height: 44px;
            background: url('../../assets/icons/icon-quotes.svg');
            background-repeat: no-repeat;
            background-position: 50% top;
            background-size: contain;
        }
    }

    &__profile {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 50px 0 0 0;
        font-style: italic;
    }

    &__info {
        margin-left: 36px;
    }

    &__name {
        font-weight: $font-weight-bold;
        font-size: 28px;
    }

    &__position {
        font-size: $font-size-md;
    }

    @include below-desktop {
        &__inner {
            padding: 0 20px;
        }

        &__quote {
            font-size: $font-size-base;

            &:before {
                height: 32px;
            }
        }

        &__img {
            max-height: 100px;
            max-width: 100px;
        }

        &__name {
            font-size: $font-size-base;
        }

        &__position {
            font-size: $font-size-sm;
        }

        &__info {
            margin-left: 28px;
        }
    }

    @include below-mobile-xs {
        &__img {
            max-height: 80px;
            max-width: 80px;
        }
    }
}

.program-card {
    display: block;
    height: 100%;

    &__inner {
        position: relative;
        display: block;
        //border: 1px solid $color01;
        background: $color-white;
        height: 100%;
        overflow: hidden;
        transition: $transition-default;
    }

    &__image {
        height: 320px;
        background-color: $color-gray-lightest;
        background-size: cover;
        background-position: 50% 50%;
        //border-bottom: 1px solid $color-gray-dark;
    }

    &__content {
        padding: 40px 60px 160px 60px;
        font-style: italic;
        font-weight: $font-weight-bold;
    }

    &__title {
        margin: 0 0 30px 0;
    }

    &__detail {
        font-weight: $font-weight-black;
    }

    &__bottom {
        position: absolute;
        bottom: 48px;
        left: 0; right: 0;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 60px;
    }

    &__price {
        margin: 8px 20px 0 0;
    }

    &--bordered {
        .program-card__inner {
            border: 2px solid $color-gray-lightest;
        }
        
    }

    &:hover {
        color: $color-text;

        .program-card__button {
            background: $color01-light;
            transition: $transition-default;

			.icon {
				animation-name: buttonIconBounceRight;
				animation-duration: $trans-time-md;
				animation-direction: forwards;
			}
        }
    }


    @include above-mobile {     
        &:hover {
            .program-card__inner {
                transform: translateY(-20px);
                transition: $transition-default;
            }
        }
    }

    @include below-desktop-lg {
        &__content {
            padding: 40px 40px 160px 40px;
        }

        &__bottom {
            padding: 0 40px;
        }

        &__title {
            font-size: $font-size-md;
        }
    }

    @include below-mobile-sm {
        &__bottom {
            flex-wrap: wrap;
        }

        &__content {
            padding-bottom: 200px;
        }

        &__price {
            margin-bottom: 8px;
        }

        &__button {
            margin-left: 4px;
        }
    }
}

.product-card {
    &__inner {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        transition: $transition-default;
    }

    &__bubble {
        position: absolute;
        top: 10px;
        right: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px;
        width: 70px;
        height: 70px;
        background: $color-negative;
        border-radius: 45px;
        color: $color-white;
        font-style: italic;
        font-weight: $font-weight-bold;
    }

    &__image {
        position: relative;
        max-width: 320px;
        background: $color-gray-lightest;
        border: 1px solid $color-gray;
        margin: 0 0 28px 0;
    }

    &__title {
        font-size: $font-size-md;
        margin: 0 0 12px 0;

    }

    &__button {
        margin: 18px 0 0 0;
        max-width: 320px;
    }

    &--sm {
        .product-card__title {
            font-size: $font-size-base;
        }

        .product-card__button {
            display: none;
        }

        .product-card__price {
            .price__main {
                font-size: $font-size-base;
            }

            .price__old, .price__new {
                font-size: $font-size-base;
            }
        }
    }

    &:hover {
        color: $color-text;

        .product-card__button {
            background: $color01-light;
            border: 1px solid $color01-light;
        }

        .icon {
            animation-name: buttonIconBounceRight;
            animation-duration: $trans-time-md;
            animation-direction: forwards;
        }
    }


    @include above-mobile {     
        &:hover {
            .product-card__inner {
                transform: translateY(-20px);
                transition: $transition-default;
            }
        }
    }

    @include below-desktop {
        &__title {
            font-size: $font-size-md;
        }
    }
}

.trainer-card {
    &__header {
        display: flex;
        align-items: center;
        margin: 0 -20px;
    }

    &__photo {
        padding: 0 20px;
    }

    &__info {
        padding: 0 20px;
    }

    &__position {
        font-size: $font-size-xs;
        font-style: italic;
    }

    &__name {
        margin: 0;
        font-size: $font-size-lg;
        text-transform: none;
        font-weight: $font-weight-black;
    }

    &__content {
        margin-top: 30px;
        font-size: $font-size-sm;
    }

    @include below-mobile {
        &__name {
            font-size: 28px;
        }

        &__position {
            font-size: 12px;
        }
    }

    @include below-mobile-sm {
        &__header {
            flex-direction: column;
        }

        &__photo {
            margin-bottom: 32px;
        }
    }
}

.module-nav-item {
    font-weight: $font-weight-bold;
    font-style: italic;
    text-transform: uppercase;

    &__inner {
        position: relative;
        display: flex;
        align-items: center;
        padding: 16px 24px 16px 64px;
        min-height: 64px;
        border: 1px solid $color-gray-light;
        transition: $transition-default;

        &:hover {
            transform: translateX(8px);
            opacity: $opacity-md;
            transition: $transition-default;
        }
    }

    &__title {
        margin: 0;
        line-height: 1;
        font-size: $font-size-xs;
    }

    &__icon {
        position: absolute;
        height: 24px;
        width: 24px;
        left: 24px;
        top: 19px;
    }

    &__submenu {
        &.is-active {
            padding-top: 12px;
        }

        > .module-nav-item {
            padding-left: 36px;
            transition: $transition-default;
        }
        > .module-nav-item--locked {
            .module-nav-item__inner {
                &:hover {
                    transform: translateX(0);
                }
            }
        }
    }

    &--group {
        > .module-nav-item__inner {
            cursor: pointer;
            padding-left: 24px;
            padding-right: 64px;

            .module-nav-item__icon {
                height: 16px;
                width: 16px;
                left: initial;
                right: 24px;
                top: 22px;
            }

            &:hover {
                transform: translateX(0);
                opacity: 1;
            }
        }

        &.is-active {
            > .module-nav-item__inner {
                .module-nav-item__icon {
                    transform: rotate(-180deg);
                }
            }
        }
    }

    &--completed {
        .module-nav-item__inner {
            color: $color-white;
            background: $color-positive;
            border: 1px solid darken($color-positive, 6%);
        }
    }

    &--locked {
        .module-nav-item__inner {
            color: $color-gray-darker;
            background: $color-gray-lighter;
            border: 1px solid $color-gray-light;
            cursor: default;
            pointer-events: none;

            &:hover {
                opacity: 1;
            }
        }
    }

    .module-nav-item--current {
        padding-right: 8px;

        > .module-nav-item__inner {
            border: 1px solid $color-black;
            pointer-events: none;
            cursor: default;
            
            &:before {
                content: "";
                position: absolute;
                top: calc(50% - 8px);
                left: -36px;
                width: 16px;
                height: 16px;
                background-image: url('../../assets/icons/icon-arrow-right.svg');
                background-repeat: no-repeat;
                background-size: contain;
            }

            &:hover {
                color: $color-text !important;
                opacity: 1 !important;
                transform: translateX(0) !important;
            }
        }
    }

    @include below-desktop {
        .module-nav-item--current {
            padding-right: 0;
        }
    }
}