/******************************************************************
GALLERY STYLES
Styling for gallery component
******************************************************************/


.gallery {
    $self: &;

    list-style-type: none;
    padding: 0;

    &__item {
        max-width: 25%;
        
        IMG {
            display: block;
            width: 100%;
            //background-color: $color-gray-lightest;
            //border: 2px solid $color-gray-lightest;
            box-shadow: inset 0 0 0 2px $color-gray-lightest;
        }
        A {
            display: block;
            opacity: 1;
            transition: $transition-default;
            &:hover {
                opacity: 0.75;
                transition: $transition-default;
            }
        }
    }

    &__caption {
        display: block;
        margin: 10px 0;
        color: $color-gray-dark;
        font-size: 16px;
        font-weight: $font-weight-normal;
        text-align: center;
        word-wrap: break-word;
    }

    @include below-mobile {
        flex-wrap: wrap;
        &__item {
            max-width: 50%;
            flex: 1 0 50%;
            margin-bottom: 20px;
        }
    }
    @include below-mobile-sm {
        display: block;

        &__item {
            display: block;
            max-width: 100%;
            margin-bottom: 30px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.gallery-image {
    &__img {
        position: relative;
        width: 100%;
        padding-top: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
    }
}

.product-gallery {
    &__items {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -10px;
    }

    &__item {
        padding: 0 10px;
        flex: 1 0 33.33333%;
        max-width: 33.33333%;
        margin-bottom: 20px;

        &:first-child {
            flex: 1 0 100%;
            max-width: 100%;
        }

        &:nth-child(n+5) {
            display: none !important;
        }
    }

    &__anchor {
        display: block;
        background: $color-white;
        border: 1px solid $color-gray;
        height: 100%;
        width: 100%;
        overflow: hidden;
        transition: $transition-default;

        &:hover {
            .product-gallery__img {
                opacity: $opacity-md;
                transform: scale(1.1);
                transition: $transition-default;
            }
        }
    }

    &__img {
        display: block;
        width: 100%;
        transition: $transition-default;
    }
    
    &__more {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
        min-height: 100px;
        background: $color-white;
        font-size: $font-size-xs;
        font-weight: $font-weight-bold;
        font-style: italic;
        transition: $transition-default;

        &:hover {
            color: $color-text;
            background: $color01-light;
            border: 1px solid $color01-light;
            transition: $transition-default;
        }
    }

    &__icon {
        height: 28px;
        width: 28px;
        margin-bottom: 4px;
    }

    @include below-desktop {
        &__item {
            padding: 0 10px;
            flex: 1 0 50%;
            max-width: 50%;
        }
    }

    @include below-mobile {     
        &__item {
            margin-bottom: 20px;

            &:first-child {
                flex: 1 0 50%;
                max-width: 50%;
            }
        }
    }

    @include below-mobile-sm {
        &__item {
            &:first-child, &:nth-child(4) {
                flex: 1 0 100%;
                max-width: 100%;
            }
        }
    }

    @include below-mobile-xs {
        &__icon {
            height: 24px;
            width: 24px;
        }

        &__more {
            font-size: 12px;
        }
    }
}