.newsletter {
    #mc_embed_signup #mc-embedded-subscribe-form #mc_embed_signup_scroll {
        .mce_inline_error {
            border-radius: 0;
            padding: 12px 20px;
            min-width: 280px;
            margin-bottom: 24px;
        }
    
        .button--skewed {
            font-style: normal;
            transition: $transition-default;

            &:hover {
                color: $color-text;
                background: $color01-light !important;
                transition: $transition-default;
            }
        }
    }
}