/******************************************************************
MODULE STYLES
Reusable/modular content blocks that make up the site layout
******************************************************************/

.title {
    display: inline-block;
    position: relative;
    padding-left: 40px;
    
    &:before {
        content: "";
        position: absolute;
        left: 0; top: 1px;
        width: 8px; //6px;
        height: 1em;
        background: $color01;
        transform: skew(-11deg);
    }
    @include below-mobile-sm {
        padding-left: 25px;
    }

    @include below-mobile-xs {
        padding-left: 0;

        &:before {
            display: none;
        }
    }
}

.section {
    position: relative;
    padding: 180px 0;

    &--padding-sm {
        padding: 130px 0 140px 0;
    }

    @include below-desktop {
        padding: 120px 0;
    }

    @include below-mobile-sm {
        padding: 80px 0;
    }
}

.section-heading {
    margin-bottom: 96px;

    &__title {
        margin: 0;
    }

    // MODIFIERS
    &--has-options {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .section-heading__options {
            margin-left: 64px;
        }
    }

    @include below-desktop {
        text-align: center;
    }

    @include below-mobile {
        margin-bottom: 64px;

        &--has-options {
            display: block;

            .section-heading__title {
                margin-bottom: 20px;
            }

            .section-heading__options {
                margin-left: 0;
            }
        }
    }

    @include below-mobile-sm {
        margin-bottom: 48px;
    }
}

.dual-bg {
    position: absolute;
    left: 0; top: 0; right: 0; bottom: 0;
    display: flex;
    width: 100%;
    height: 100%;
    z-index: -1;
    font-size: 0;
    color: rgba(0, 0, 0, 0);
    overflow: hidden;

    &:before {
        content: "";
        flex: 2;
        background: $color01-light;
    }

    &:after {
        content: "";
        flex: 5;
        background: $color-gray-lightest;
    }

    // MODS
    &--reverse {
        flex-direction: row-reverse;
    }
}

.price {
    font-size: 32px;
    font-weight: $font-weight-bold;
    font-style: italic;
    line-height: 1;
    color: $color01;
    display: flex;
    flex-direction: column;

    &__main {
        display: flex;
        align-items: center;
        margin: 0 -6px;
    }

    &__old, &__new {
        padding: 0 6px;
        margin-bottom: 8px;
    }

    &__standard {
        display: inline-block;
        margin-bottom: 8px;
    }

    &__old {
        font-weight: $font-weight-bold;
        text-decoration: line-through;
        color: $color-gray;
    }

    &__new {
        color: $color-negative;
    }

    &__title {
        color: $color-text;
        font-size: $font-size-sm;
        margin: 0 8px 8px 0;
    }

    &__date {
        margin-top: -4px;
        font-size: 14px;
        font-weight: $font-weight-bold;
        color: $color-negative;
    }

    @include below-desktop {
        font-size: $font-size-md;
        
        .price__old, .price__new {
            font-size: 28px;
        }
    }

    &--product {
        .price__main {
            margin: 0;
        }

        .price__old {
            order: 3;
        }
    }

    &--product-card {
        .price__main {
            flex-direction: row-reverse;
            justify-content: flex-end;
            font-size: 28px;
        }

        .price__old {
            font-size: $font-size-md;
        }   
    }

    &--program-card {
        font-weight: $font-weight-black;
        line-height: 1;

        .price__main {
            flex-direction: row-reverse;
            justify-content: flex-end;
            margin: 0 -8px;
            color: $color-text;
            font-size: 42px;
        }

        .price__standard {
            padding: 0 8px;
        }

        .price__old, .price__new {
            padding: 0 8px;
        }

        .price__new {
            font-size: 42px;
        }

        .price__old {
            font-size: 28px;
        }

        @include range-desktop-to-desktop-lg {
            .price__main, .price__new {
                font-size: 32px;
            }
        }

        @include below-desktop {
            .price__main, .price__new {
                font-size: $font-size-lg;
            }
        }
    }

    &--program {
        font-weight: $font-weight-black;
        line-height: 1;

        .price__main {
            flex-direction: row-reverse;
            justify-content: flex-end;
            margin: 0 -8px;
            color: $color-text;
            font-size: 42px;
        }

        .price__standard {
            padding: 0 8px;
        }

        .price__old, .price__new {
            padding: 0 8px;
        }

        .price__old {
            font-size: $font-size-lg;
        }

        .price__title {
            margin: 0 8px 0 0;
        }

        .price__prefix {
            color: $color-text;
            font-size: $font-size-lg;
        }
    }
}

.social-share {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    &__title {
        margin: 0 40px 16px 0;
        font-weight: $font-weight-bold;
        font-style: italic;
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 -8px;
    }

    &__item {
        padding: 0 8px;
        margin-bottom: 16px;
    }

    &__anchor {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 60px;
        width: 60px;
        border: 1px solid $color-gray-lighter;
        background: $color-white;
        color: $color-text;
        border-radius: 30px;
        transition: $transition-default;

        &:hover {
            color: $color-text;
            background: $color01-light;
            border: 1px solid $color01-light;
            transition: $transition-default;
        }
    }

    &__icon {
        width: 24px;
        height: 24px;
    }

    @include below-mobile {
        flex-direction: column;

        &__title {
            margin: 0 0 12px 0;
        }

        &__anchor {
            height: 48px;
            width: 48px;
        }

        &__icon {
            width: 18px;
            height: 18px;
        }
    }
}

.label {
    display: inline-block;
	background: $color-white;
	padding: 16px 32px;
	min-width: 56px;
    font-weight: $font-weight-bold;
    font-style: italic;
    line-height: 1;
    text-align: center;
	transform: skew(-11deg);

    &__text {
        display: block;
        transform: skew(11deg);
    }

    @include below-mobile-sm {
        padding: 12px 24px;
    }
}

.product-quantity {
    display: flex;

    .qty {
        display: inline-block;
        margin: 0;
        max-width: 120px;
        min-width: 120px;
    }

    &__button {
        display: inline-block;
        padding: 16px;
        border: 1px solid $color-gray;
        transition: $transition-default;
        
        &:first-child {
            border-right: 0;
        }
        &:last-child {
            border-left: 0;
        }

        &:hover {
            color: $color-text;
            background: $color01-light;
            border: 1px solid $color01-light;
            transition: $transition-default;

            &:first-child {
                border-right: 0;
            }
            &:last-child {
                border-left: 0;
            }
        }
    }
}

.product-cart {
    display: flex;
    align-items: center;
    margin: 0 -8px;

    .single_add_to_cart_button {
        min-height: 60px;
    }

    &__quantity, &__options {
        padding: 0 8px;
    }

    @include below-desktop {
        flex-wrap: wrap;
        
        &__quantity {
            margin-bottom: 8px;
        }
    }
}

.related-products {
    .related-products__heading {
        margin: 0 0 36px 0;
    }

    &__items {
        padding-top: 20px;
    }

    @include below-mobile {
        .related-products__heading {
            text-align: left;

            .section-heading__title {
                font-size: 24px;
            }
        }
        
    }
}

.product-extra {
    &__nav {
        margin-bottom: 40px;
    }
}

.sidebar-price {
    position: relative;
    display: flex;
    align-items: center;
    background: $color01-light;
    padding: 33px 48px;
    min-height: 146px;
    margin-bottom: 68px;

    &__inner {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 -15px;
    }

    &__price, &__join {
        padding: 0 15px;
    }

    &__button {
        //width: 100%;
        min-width: 240px;
    }

    &__error {
        padding: 0 15px;
    }

    &__error-main {
        font-size: $font-size-md;
        font-weight: $font-weight-black;
        font-style: italic;
        line-height: $line-height-sm;
        margin: 0 0 8px 0;
    }

    &__error-sub {
        font-size: $font-size-sm;
        font-style: italic;
        line-height: $line-height-sm;
        margin: 0;
    }

    @include below-desktop {
        display: none;
    }
}

.sidebar-overview {
    background: $color-white;
    padding: 60px 48px 68px 48px;
    margin-bottom: 30px;
    font-size: $font-size-sm;

    &__title {
        font-size: $font-size-md;
        text-transform: none;
        margin-bottom: 60px;
    }

    @include below-mobile {
        padding: 54px 40px;
    }

    @include below-mobile-sm {
        padding: 42px 24px 48px 24px;
        //margin-bottom: 0;
    }
}

.sidebar-modules {
    background: $color-white;
    padding: 60px 48px 68px 48px;
    margin-bottom: 30px;
    font-size: $font-size-sm;

    &__title {
        font-size: 24px;
        text-transform: none;
        margin-bottom: 24px;
    }

    &__bar {
        margin-bottom: 60px;
    }

    &__options {
        margin-top: 16px;
        //text-align: center;
    }

    &__back {
        position: relative;
        padding-right: 28px;
        font-size: $font-size-xs;
        color: $color-gray-light;

        &:hover {
            color: $color-text;
        }
    }

    &__toggle {
        display: none;
    }

    &__back-icon {
        position: absolute;
        right: 0; top: calc(50% - 8px);
        height: 16px;
        width: 16px;
    }

    @include below-desktop {
        position: fixed;
        left: 0; right: 0;
        bottom: 0;
        margin: 0;
        background: transparent;
        margin: 0 40px;
        padding: 0;
        box-shadow: $shadow02;
        transform: translateY(calc(100% - 79px));
        transition: $transition-default;
        z-index: 1;

        &__toggle {
            display: block;
        }

        &__inner {
            padding: 36px 24px 24px 24px;
            background: $color-white;
            z-index: 2;
            border-left: 1px solid $color-gray-light;
            border-right: 1px solid $color-gray-light;
        }

        &__nav {
            height: 40vh;
            overflow-y: auto;
        }

        &__title {
            display: none;
        }

        &__bar {
            margin-bottom: 24px;
        }

        &__options {
            margin-top: 20px;
            text-align: center;
        }

        &.is-drawer-open {
            transform: translateY(0);
            transition: $transition-default;
        }
    }

    @include below-mobile-sm {
        margin: 0 20px;
    }
}

.sidebar-materials {
    background: $color-white;
    padding: 60px 48px 68px 48px;
    margin-bottom: 30px;
    font-size: $font-size-sm;

    &__title {
        font-size: $font-size-md;
        text-transform: none;
        margin-bottom: 30px;
    }

    &__text {
        margin-bottom: 60px;
    }

    @include below-mobile {
        padding: 54px 40px;
    }

    @include below-mobile-sm {
        padding: 42px 24px 48px 24px;
        //margin-bottom: 0;
    }
}

.progress-bar {
    position: relative;
    height: 66px;
    width: 100%;
    padding: 3px;
    border: 1px solid $color-gray-light;

    &__inner {
        width: 50%;
        height: 100%;
        background: $color-positive;
        font-size: 0;
    }

    &__number {
        position: absolute;
        top: 16px;
        right: 22px;
        font-style: italic;

        &--light {
            color: $color-white;
            right: initial;
            left: 22px;
        }
    }

    @include below-desktop {
        height: 16px;
        padding: 2px;
        border-radius: 12px;

        &__number {
            display: none;
        }

        &__inner {
            border-radius: 10px;
        }
    }
}

.circular-image {
    max-height: 132px;
    max-width: 132px;
    border: 1px solid $color-gray-lighter;
    border-radius: 132px;
    overflow: hidden;

    &--lg {
        max-height: 148px;
        max-width: 148px;
    }
}

.content-readmore {
    position: relative;
    height: 400px;
    overflow-y: hidden;
    z-index: 0;

    &__options {
        position: absolute;
        left: 0; right: 0;
        bottom: 0;
        width: 100%;
        text-align: center;
        z-index: 2;
    }

    &:before {
        content: "";
        position: absolute;
        left: 0; right: 0; bottom: 0;
        height: calc(100% - 100px);
        width: 100%;
        background: linear-gradient(0deg, rgba($color-white, 1) 0%, rgba($color-white, 0.85) 75%, rgba($color-white, 0) 100%);
        z-index: 1;
    }

    > *:first-child {
		margin-top: 0;
	}
	> *:last-child {
		margin-bottom: 0;
	}

    &.is-open {
        height: auto;

        .content-readmore__options, &:before {
            display: none;
        }
    }
}

.sticky-cart {
    display: block;
    padding: 20px 32px 20px 80px;
    position: relative;
    background: $color-white;
    box-shadow: $shadow02;
    font-style: italic;

    &__icon-wrap {
        position: absolute;
        left: 24px;
        top: calc(50% - 20px);
        height: 40px;
        width: 40px;
        background: $color01-light; //$color-gray-lighter;
        border-radius: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__icon {
        width: 24px;
        height: 24px;
    }

    &__message {
        margin: 0;
    }

    &__amount {
        font-weight: $font-weight-bold;
        margin-right: 4px;
    }

    @include below-desktop-lg {
        display: block;
        height: 40px;
        width: 40px;
        padding: 0;
        box-shadow: none;
        background: transparent;

        &__icon-wrap {
            position: relative;
            top: initial; left: initial;
            background: transparent;
        }

        &__icon {      
            height: 36px;
            width: 36px;
        }

        &__text {
            display: none;
        }

        &__amount {
            position: absolute;
            top: -5px; right: -10px;
            display: block;
            background: $color01-light;
            height: 24px;
            width: 24px;
            border-radius: 20px;
            font-size: $font-size-xs;
            text-align: center;
            margin: 0;
        }
    }
}

.debug-user {
    &__accordion {
        width: 100%;
    }

    &__content {
        padding: 20px 0;
    }

    &__content-item {
        margin-bottom: 24px;
    }
}

.notification-bar {
    width: 100%;
    padding: 20px 0;
    background: $color01-light;

    &__text {
        margin: 0;
    }

    &--positive {
        background: $color-positive;
        color: $color-white;
    }

    &--negative {
        background: $color-negative;
        color: $color-white;
    }
}

.text-with-icon {
    position: relative;
    padding-left: 40px;

    &__icon {
        position: absolute;
        left: 0;
        top: 4px;
        height: 24px;
        width: 24px;
    }
}