/* PRODUCT SINGLE */
.product {
    .variations {
        //margin: 0 0 36px 0;

        .label {
            border: 0;
            transform: none;
            padding: 0;
            margin: 32px 0;
        }

        LABEL {
            font-weight: $font-weight-bold;
        }

        TD, TR {
            border: none;
        }

        SELECT {
            margin: 0;
        }

        .reset_variations {
            margin: 4px 0 0 0;
            font-size: $font-size-xs;
            text-decoration: underline; 
            font-style: italic;
        }

        .value {
            padding: 8px 20px;
        }
    }

    .woocommerce-variation-price {
        margin: 0 0 20px 0;
    }
}


/* NOTICES */
.woocommerce-message, .woocommerce-info {
	background: $color-gray-lightest;
	padding: 20px 30px !important;
	margin-bottom: 36px;

    A {
        &:not(.button) {
            text-decoration: underline;
        }
    }

    .button {
        margin-right: 16px;
    }
}

.woocommerce-error {
	background: $color-negative;
	color: $color-white;
	padding: 20px 30px !important;
}

UL.woocommerce-error {
	LI {
		padding-left: 0;

		&:before {
			display: none;
		}
	}

	.button {
		margin-right: 20px;
	}
}


