/* ACCOUNT */
.woocommerce-privacy-policy-text {
    font-size: $font-size-xs;

    A {
        text-decoration: underline;
    }
}

.woocommerce-password-strength {
    margin-bottom: 4px;

    &.short, &.bad {
        color: $color-negative;
    }
    &.good {
        color: $color-neutral;
        margin-bottom: 24px; // This rating has no hint, so move margin here
    }
    &.strong {
        color: $color-positive;
        margin-bottom: 24px; // This rating has no hint, so move margin here
    }
}
.woocommerce-password-hint {
    display: block;
    line-height: 1.4;
    font-size: 12px;
    margin-bottom: 24px;
}


/* CART */
.cart-table {
	&__thumbnail {
		IMG {
			max-width: 80px;
			max-height: 80px;
			margin: 0;
		}
	}

	&__name {
		min-width: 240px;
		line-height: 1.2;

        A {
            text-decoration: underline;
        }
	}

	&__quantity {
		INPUT {
			margin: 0;
			max-width: 100px;
            min-width: 100px;
		}
	}

	&__mobile-title {
		display: none;
		font-weight: $font-weight-bold;
		line-height: $line-height-base;
	}

	TBODY {
		TD {
			padding: 24px 20px;
		}
	}

	@include below-desktop {
		&__name {
			min-width: 160px;
		}
	}

	@include below-mobile {
		&__mobile-title {
			display: block;
		}

		&__item {
			text-align: center;
		}

		&__heading {
			display: none;
		}

		&__thumbnail {
			display: none;
		}

		&__quantity {
			.quantity INPUT {
				margin: 8px 0 0 0;
			}
		}

		&__name, &__price, &__quantity, &__subtotal {
			border-bottom: 0;
			padding-bottom: 0;
		}

		&__name, &__price, &__quantity, &__subtotal, &__remove {
			display: block; 
		}
	}
}

.product-remove {
	&__button {
		color: $color-negative;
	}

	&__desktop-text {
		display: inline-block;
        font-size: 28px;
	}
	&__mobile-text {
		display: none;
	}

	@include below-mobile {
		&__desktop-text {
			display: none;
		}
		&__mobile-text {
			display: inline-block;
		}
	}
}

.coupon {
    display: flex;
    margin: 0 -8px;
	flex-wrap: wrap;

    &__field {
        flex: 1;
        padding: 0 8px;
		margin-bottom: 8px;
        
        .text-field__input {
            margin: 0;
        }
    }

    &__button {
        padding: 0 8px;

        .button {
            min-height: 60px;
        }
    }

	@include below-mobile-sm {
		&__button {
			width: 100%;
		}
	}
}

.wc-proceed-to-checkout {
    text-align: right;
    margin: 24px 0 0 0;
}

.cart-collaterals {
    margin: 40px 0 0 0;

	.woocommerce-shipping-totals {
		display: none;
	}
	
}

.cart-empty {
	text-align: center;
	color: $color-negative;
}

.product-remove .remove {
	text-decoration: none;
}

.woocommerce-cart .woocommerce-error .button.wc-forward {
	display: none;
}


/* CHECKOUT */
.checkout-options {
	margin: 36px -8px 0 -8px;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;

	&__item {
		padding: 0 8px;
		margin-bottom: 8px;
	}

	@include below-mobile-sm {
		&__item {
			flex: 1;
		}
	}
}

.wc_payment_methods {
	list-style-type: none !important;
	margin: 48px 0 !important;
	padding: 36px 0 !important;
	border-top: 1px solid $color-gray-light !important;
	border-bottom: 1px solid $color-gray-light !important;

	LI.wc_payment_method {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		background: $color-gray-lightest;
		border: 1px solid $color-gray-lighter;
		padding: 20px;
		margin-bottom: 20px;

		P {
			margin: 0 !important;
		}

		.payment_box {
			padding-top: 25px;
			flex: 1 0 100%;
			max-width: 100%;
		}

		&:before {
			display: none;
		}

		IMG {
			margin-bottom: 0;
			
			&#montonio-payments-checkout-logo {
				width: auto;
				height: auto;
				max-height: 25px;
				padding: 3px;
			}

			&#montonio-montonio_split_1-checkout-logo {
				width: auto;
				height: auto;
				max-height: 25px;
				padding: 3px;
			}

			&#montonio-montonio_split_3-checkout-logo {
				width: auto;
				height: auto;
				max-height: 25px;
				padding: 3px;
			}
		}

		SELECT {
			min-height: unset;
		}

		.input-radio {
			margin-right: 15px;
			margin-bottom: 3px;
		}

		LABEL {
			display: flex;
			flex-direction: row-reverse;
			justify-content: flex-end;
			margin-bottom: unset;
		}

		.montonio-aspsp-grid-item {
			border-radius: unset;
			border: 1px solid $color-gray;
		}
	}

    LI.woocommerce-notice {
        &:before {
            display: none;
        }
    }
}

.woocommerce-billing-fields__field-wrapper, .woocommerce-additional-fields {
    margin: 0 0 80px 0;
}

.woocommerce-terms-and-conditions-wrapper {
    margin: 0 0 60px 0;
}

.woocommerce-checkout-review-order-table .order-total TD:first-child {
    background: $color-gray-lightest;
}

.checkout-place-order {
    text-align: center;
	margin-bottom: 40px;
}


/* MISC */
.form-row .required {
	color: $color-gray;
	text-decoration: none;
}

.form-row {
	position: relative; 
	
	.woocommerce-form__input-checkbox {
		@extend .checkbox__input;
	}
	.woocommerce-form__label-for-checkbox {
		@extend .checkbox__label;
	}
}

@include below-desktop {
	.woocommerce table.woocommerce-orders-table thead {
		display: none;
	}
	.woocommerce table.woocommerce-orders-table tr {
		display: block;
		margin-bottom: 30px;
	}
	.woocommerce table.woocommerce-orders-table td {
		display: block;
		text-align: right !important;
		&:not(:last-child) {
			border-bottom: 0;
		}
	}
	.woocommerce table.woocommerce-orders-table td::before {
		content: attr(data-title) ": ";
		font-weight: 700;
		float: left;
	}
}	

.my_account_orders .shop_table .woocommerce-button {
	margin-right: 8px;
}