.slider {
    overflow: hidden;

    &__items {
        display: flex;
    }

    &__item {
        flex: 1;
    }
}

.slider-nav {
    display: flex;
    padding: 8px;

    &__item {
        position: relative;
        display: inline-block;
        margin: 0 20px;
        padding: 0;
        height: 18px;
        width: 18px;
        background: rgba($color-black, 0);
        border: 1px solid $color-black;
        border-radius: 50px;
        transition: $transition-default;

        &:hover {
            background: rgba($color-black, 1);
            transition: $transition-default;
        }

        &:before {
            content: "";
            position: absolute;
            top: -8px;
            left: -8px;
            width: 32px;
            height: 32px;
            background: transparent;
            border: 1px solid rgba($color-black, 0);
            border-radius: 50px;
            transform: scale(0.75);
            transition: $transition-default;
        }

        &.is-active {
            background: rgba($color-black, 1);
            transition: $transition-default;
    
            &:before {
                border: 1px solid rgba($color-black, 1);
                transform: scale(1);
                transition: $transition-default;
            }
        }
    }

    &__arrow {
        display: flex;
        align-items: center;
    }

    &--arrows {
        padding: 8px 0;
        margin: 0 -8px;

        .slider-nav__arrow {
            padding: 0 8px;
        }

        .slider-nav__icon {
            height: 28px;
            width: 28px;
        }
    }

    @include below-mobile {
        flex-wrap: wrap;

        &__item {
            margin: 0 14px 14px 14px;
            width: 14px;
            height: 14px;

            &:before {
                width: 28px;
                height: 28px;
            }
        }
    }
}