/******************************************************************
CONTENT
Main content blocks and sections that make up the site layout
******************************************************************/

.hero {
    position: relative;
    min-height: 640px;
    max-height: 1080px;
    height: calc(100vh - 130px);
    background: $color-gray-lighter;
    z-index: 0;

    &:before {
        content: "";
        position: absolute;
        left: 0; top: 0;
        display: block;
        height: 100%;
        width: 75%;
        background: $color-gray-lightest;
        background: linear-gradient(90deg, rgba($color-gray-lightest, 1) 33%, rgba($color-gray-lightest, 0) 100%);
        z-index: 1;
    }

    &:after {
        content: "";
        position: absolute;
        top: -100px; right: -160px;
        height: 1200px;
        width: 600px;
        background-image: url('../../assets/images/hero-symbol.svg');
        background-repeat: no-repeat;
        background-size: contain;
        z-index: 2;
    }

    &__container {
        height: 100%;
    }

    &__backgrounds {
        overflow: hidden;
        z-index: -1;
    }

    &__background {
        position: absolute;
        left: 0; top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        font-size: 0;

        opacity: 0;
        visibility: hidden;
        transform: translateX(80px);
        transition: visibility $trans-time-lg $trans-type, opacity $trans-time-lg $trans-type, transform $trans-time-lg $trans-type;

        &.is-active {
            opacity: 1;
            visibility: visible;
            transform: translateX(0);
            transition: visibility $trans-time-lg $trans-type, opacity $trans-time-lg $trans-type, transform $trans-time-lg $trans-type;
        }
    }

    &__inner {
        position: relative;
        height: 100%;
    }

    &__contents {
        height: 100%;
    }

    &__content {
        width: 560px;
        padding-bottom: 100px;
        position: absolute;
        left: 0; top: 50%;
        transform: translateY(-50%);
        z-index: 1;

        opacity: 0;
        visibility: hidden;
        transform: translateY(-50%) translateX(-80px);
        transition: visibility $trans-time-md $trans-type, opacity $trans-time-md $trans-type, transform $trans-time-lg $trans-type;

        &.is-active {
            opacity: 1;
            visibility: visible;
            transform: translateY(-50%) translateX(0);
            transition: visibility $trans-time-lg $trans-type, opacity $trans-time-lg $trans-type, transform $trans-time-lg $trans-type;
        }
    }

    &__title {
        line-height: 1.2;
        margin: 0 0 40px 0;
    }

    &__subtitle {
        font-size: 26px;
        margin: 0;
    }

    &__options {
        margin: 40px 0 0 0;
    }

    &__dots {
        position: absolute;
        bottom: 50px; left: -20px;
        z-index: 1;
    }

    &--no-slides {
        .hero__contents {
            padding-bottom: 20px;
        }
    }

    @include below-desktop {
        &:before {
            width: 90%;
        }

        &:after {
            right: -240px;
        }

        &__content {
            width: 480px;
        }

        &__subtitle {
            font-size: $font-size-base;
        }
    }

    @include below-mobile {
        min-height: 600px;
        height: calc(100vh - 90px);

        &:before {
            width: 100%;
            background: rgba($color-gray-lightest, 0.86);
        }

        &:after {
            display: none;
        }

        &__content {
            text-align: center;
            width: 100%;
            max-width: 640px;
            margin: 0 auto;
        }

        &__background {
            background-position: 50% 50%;
        }

        &__dots {
            left: 0; right: 0;
            width: 100%;
            display: flex;
            justify-content: center;
        }
    }
}

.about {
    &__cols {
        display: flex;
        margin: 0 -40px;

        &:not(:last-child) {
            padding-bottom: 75px;
            border-bottom: 1px solid $color-gray-lighter;
            margin-bottom: 75px;
        }
    }

    &__col {
        padding: 0 40px;

        &:nth-child(1) {
            flex: 2;
        }

        &:nth-child(2) {
            flex: 3;
        }
    }

    &__description {
        margin: 0;
    }

    @include below-desktop {
        &__col {
            &:nth-child(1) {
                flex: 1;
            }
        }

        &__title {
            font-size: $font-size-h3;
        }
    }

    @include below-mobile {
        &__cols {
            display: block;

            &:not(:last-child) {
                padding-bottom: 0;
                border-bottom: none;
                margin-bottom: 64px;
            }
        }
    }

    @include below-mobile-sm {
        &__title {
            font-size: 28px;
        }
    }
}

.programs {
    &__filters {
        margin: 0;
    }

    &__content {
        margin: 60px 0 0 0;
    }

    &__options {
        margin-top: 20px;
        text-align: right;
    }

    &__alert {
        margin-bottom: 80px;
    }

    @include below-desktop {
        &__options {
            text-align: center;
        }
    }

    @include below-mobile {
        &__tabs {
            padding: 36px 0;
            background: $color-white;
        }
    }

    @include below-mobile-sm {
        &__options {
            margin-top: 0;
        }
    }
}

.testimonials {
    .testimonials__heading {
        text-align: center;
        margin-bottom: 64px;
    }

    &__dots {
        margin-top: 60px;
        justify-content: center;
    }

    @include below-mobile {
        &__heading {
            text-align: left;
        }
    }
}

.blog-preview {
    &__title {
        margin: 0 0 100px 0;
    }

    &__options {
        margin: 80px 0 20px 0;
        text-align: center;
    }

    @include below-mobile {
        .blog-preview__title {
            margin-bottom: 40px !important;
        }

        &__options {
            margin-top: 48px;
        }

        &__tabs {
            padding: 36px 0;
            background: $color-white;
        }
    }
}

.trainer {
    position: relative;
    background-image: url('../../assets/images/trainer-egle.png');
    background-repeat: no-repeat;
    background-position: 78% 100%;

    .trainer__content {
        font-size: $font-size-xs;
        padding-right: 640px;
        line-height: 2;
    }

    &__avatar {
        display: none;
        width: 160px;
        height: 160px;
        border-radius: 80px;
        //border: 1px solid $color-gray;
        overflow: hidden;
        margin-bottom: 30px;
    }

    &__title {
        margin: 0 0 50px 0;
    }

    &__options {
        margin: 45px 0 20px 0;
    }

    @include below-desktop-lg {
        background-position: 96% 100%;

        .trainer__content {
            padding-right: 360px;
        }
    }

    @include below-desktop {
        text-align: center;
        background-image: none;

        &__avatar {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }

        .trainer__content {
            padding-right: 0;
        }
    }
}

.shop {
    &__options {
        //margin: 80px 0 0 0;
        text-align: center;
    }

    &__pagination {
        margin: 20px 0 40px 0;
    }

    @include below-mobile {
        &__options {
            margin-top: -16px;
        }
    }
}

.page-heading {
    position: relative;
    padding: 120px 0 240px 0;
    min-height: 420px;
    background-color: $color-gray-lightest;
    background-size: cover;
    background-repeat: no-repeat;
    //border-bottom: 1px solid $color-gray;
    z-index: 0;

    &:before {
        content: "";
        position: absolute;
        left: 0; top: 0;
        display: block;
        height: 100%;
        width: 75%;
        background: $color-gray-lightest;
        background: linear-gradient(90deg, rgba($color-gray-lightest, 1) 33%, rgba($color-gray-lightest, 0) 100%);
        z-index: 1;
    }

    &__breadcrumb {
        margin: 0 0 20px 0;
    }

    &__title {
        margin: 0;
    }

    &__tags {
        margin-top: 30px;
        justify-content: center;
    }

    &__inner {
        position: relative;
        z-index: 2;
    }

    &__main {
        flex: 6;
        padding: 0 15px;
    }

    &__aside {
        flex: 3;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 15px;
    }

    &__video {
        position: absolute;
        left: 0; right: 0;
        top: 0; bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }


    &--centered {
        text-align: center;
    }

    &--program {
        min-height: 560px;
        padding-top: 140px;

        .page-heading__inner {
            display: flex;
            margin: 0 -15px;
        }

        @include below-mobile {
            .page-heading__inner {
                flex-direction: column;
            }

            .page-heading__main {
                margin-bottom: 20px;
            }
        }
    }

    @include below-desktop {
        &:before {
            width: 100%;
        }
    }

    @include below-mobile {
        padding: 96px 0;
        min-height: 120px;

        &:before {
            background: rgba($color-gray-lightest, 0.86);
        }

        &__breadcrumb, &__title {
            text-align: center;
        }

        &__breadcrumb {
            margin-bottom: 8px;
        }
    }

    @include below-mobile-xs {
        padding: 80px 0;
    }
}

.page-content {
    position: relative;
    padding: 1px 0 180px 0;
    background: $color-gray-lighter;

    &__inner {
        margin-top: -140px;
        padding: 70px;
        min-height: 180px;
        background: $color-white;
        //border: 1px solid $color-gray;
        z-index: 1;
    }

    &__heading {
        padding-bottom: 48px;
        border-bottom: 1px solid $color-gray-lightest;
        margin-bottom: 48px;
    }

    &__banner {
        margin-bottom: 48px;
    }

    &__featured-img {
        margin: 0 auto;
        max-height: 420px;
    }

    &__lead {
        font-size: $font-size-md;
        font-weight: $font-weight-bold;
        font-style: italic;
    }

    &__socials {
        padding: 50px 70px;
        background: $color-white;
    }

    &__filters {
        margin: 0 0 80px 0;
    }

    &--transparent {
        .page-content__inner {
            background: transparent;
            padding: 0;
        }
    }

    &--with-sidebar {
        .page-content__inner {
            display: flex;
            margin: -180px -15px 0 -15px;
            padding: 0;
            background: transparent;
        }

        .page-content__main {
            flex: 6;
            padding: 0 15px;
            min-width: 0;
        }

        .page-content__aside {
            flex: 3;
            padding: 0 15px;
            min-width: 0;
        }

        .page-content__block {
            padding: 70px 70px 96px 70px;
            min-height: 240px;
            background: $color-white;
            margin-bottom: 30px;

            &--sm {
                padding: 40px 70px;
                min-height: 120px;
            }

            &--md {
                padding: 56px 70px;
                min-height: 120px;
            }
        }

        @include below-desktop-lg {
            .page-content__inner {
                margin-left: -10px;
                margin-right: -10px;
            }

            .page-content__main {
                flex: 5;
                padding: 0 10px;
            }

            .page-content__aside {
                padding: 0 10px;
            }
        }

        @include below-desktop {
            .page-content__inner {
                flex-direction: column;
            }
        }

        @include below-mobile {
            .page-content__inner {
                margin: 0 -20px;
            }

            .page-content__main, .page-content__aside {
                padding: 0;
            }

            .page-content__block {
                padding: 54px 40px;
            }
        }

        @include below-mobile-sm {
            .page-content__block {
                padding: 48px 24px;
            }
        }
    }

    @include below-mobile {
        padding: 1px 0 0 0;

        &__inner {
            margin: 0 -20px;
            padding: 54px 40px;
        }

        &__banner {
            margin-bottom: 24px;
        }

        &__heading {
            padding-bottom: 30px;
            margin-bottom: 30px;
        }

        &__lead {
            font-size: $font-size-base;
        }
    }

    @include below-mobile-sm {
        &__inner {
            padding: 48px 24px;
        }
    }
}

.product {
    &__main {
        display: flex;
        margin: 0 -40px;
    }

    &__gallery, &__content {
        padding: 0 40px;
    }

    &__gallery {
        flex: 1;
        margin-bottom: 20px;
    }

    &__content {
        flex: 2;
    }

    &__description {
        border-top: 1px solid $color-gray;
        padding: 36px 0;
    }

    &__price {
        border-top: 1px solid $color-gray;
        padding: 20px 0 12px 0;
    }

    &__options {
        border-top: 1px solid $color-gray;
        padding: 20px 0;
    }

    &__extra {
        margin: 120px 0 0 0;
    }

    &__related {
        margin: 140px 0 0 0;
    }

    @include below-desktop-lg {
        &__content {
            flex: 1;
        }
    }

    @include below-desktop {
        &__main {
            margin: 0 -24px;
        }

        &__content, &__gallery {
            padding: 0 24px;
        }
    }

    @include below-mobile {
        &__main {
            display: block;
        }
    }
}

.login {
    font-size: $font-size-sm;

    .login__title {
        margin: 0 0 36px 0;
    }

    &__options {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 24px;
    }

    &__remember-me {
        margin: 0;
    }

    &__lost-pw {
        margin-top: 2px;

        A {
            color: $color-gray;

            &:hover {
                color: $color-text;
            }
        }
    }

    @include below-desktop {
        margin-bottom: 96px;
    }

    @include below-mobile {
        margin-bottom: 72px;
    }
}

.register {
    font-size: $font-size-sm;

    .register__title {
        margin: 0 0 36px 0;
    }
}

.program {
    &__section {
        &:not(:last-child) {
            padding-bottom: 56px;
            border-bottom: 1px solid $color-gray-lighter;
            margin-bottom: 56px;
        }
    }

    &__cta {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 -20px;
    }

    &__price, &__join {
        padding: 0 20px;
    }

    &__content-tabs {
        margin-bottom: 80px;
    }

    .program__options {
        margin-top: 80px;
        text-align: center;
    }

    &__completion-message {
        margin: 0;
    }

    @include below-mobile-sm {
        &__completion-message {
            text-align: left;
        }

        &__cta {
            flex-direction: column;
        }

        &__join {
            padding: 15px 20px 0 20px;
        }
    }
}

.order-thanks {
    &__content {
        text-align: center;
    }

    .order-thanks__title {
        margin: 0 0 36px 0;
        color: $color-positive;
    }
}

.account {
    &__content {
        margin: 80px 0 0 0;
    }

    &__nav {
        margin-bottom: 68px;
    }

    &__mobile-nav {
        margin-bottom: 60px;
        display: none;
    }

    &__inner {
        padding: 0 0 40px 0;
    }

    &__block {
        margin-bottom: 60px;
    }

    &__order {
        margin-top: 60px;
    }

    &__no-programs {
        text-align: center;
    }

    &__logout {
        margin-top: 40px;
        text-align: center;

        A {
            color: $color-negative;

            &:hover {
                color: rgba($color-negative, $opacity-md);
            }
        }
    }

    @include below-mobile {
        &__mobile-nav {
            display: block;
        }

        &__nav {
            display: none;
        }

        &__logout {
            margin: 0;
            padding: 30px 0 60px 0;
        }
    }
}

.newsletter {
    padding: 80px 0 96px 0;
    background: #181818;
    color: $color-white;
    text-align: center;

    .newsletter__heading {
        text-align: center;
        margin-bottom: 40px;
    }

    &__text, &__form {
        margin: 0 auto;
        max-width: 960px;
    }

    &__form {
        margin-top: 8px;
    }

    @include below-mobile {
        &__heading {
            text-align: left;
        }
    }
}

.instagram {
    &__inner {
        position: relative;
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(5, minmax(140px, 1fr));
    }

    &__img {
        aspect-ratio: 1;
        object-fit: contain;
        width: 100%;
        height: auto;
    }

    &__label-wrap {
        position: absolute;
        left: 0; right: 0;
        top: 0; bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__label {
        position: relative;
        background: rgba($color-white, 1);
        min-width: 460px;
        min-height: 180px;
        padding: 40px 40px 50px 40px;
        text-align: center;
        text-decoration: none;

        &:hover {
            .instagram__icon {
                opacity: $opacity-md;
                transition: $transition-default;
            }
        }
    }

    &__icon {
        display: inline-block;
        height: 40px;
        width: 40px;
        margin-bottom: 4px;
        transition: $transition-default;
    }

    &__title {
        font-size: $font-size-md;
        font-weight: $font-weight-black;
        margin: 0;
        text-transform: uppercase;
    }

    &__subtitle {
        font-size: $font-size-sm;
        margin: 4px 0 0 0;
    }

    @include below-desktop {
        &__inner {
            margin: 0 -40px;
        }

        &__items {
            grid-template-columns: repeat(4, minmax(120px, 1fr));
        }

        &__item {
            &:nth-child(9), &:nth-child(10) {
                display: none;
            }
        }
    }

    @include below-mobile {
        &__inner {
            margin: 0 -20px;
        }

        &__items {
            grid-template-columns: repeat(3, minmax(100px, 1fr));
        }

        &__item {
            &:nth-child(7), &:nth-child(8) {
                display: none;
            }
        }

        &__label {
            min-width: 360px;
        }
    }

    @include below-mobile-sm {
        &__items {
            grid-template-columns: repeat(2, minmax(100px, 1fr));
        }

        &__item {
            &:nth-child(5), &:nth-child(6) {
                display: none;
            }
        }

        &__label {
            padding: 36px 30px;
            min-width: 240px;
        }

        &__subtitle {
            font-size: $font-size-base;
        }

        &__icon {
            height: 32px;
            width: 32px;
        }
    }
}


.edit-profile {
    &__display-name {
        height: 0;
        font-size: 0;
        visibility: hidden;
        opacity: 0;
    }
}