/******************************************************************
BREADCRUMB STYLES
Styles for breadcrumb components
******************************************************************/


.breadcrumbs {
    &__item {
        display: inline-block;

        &:after {
            content: "//";
            padding: 0 16px;
        }

        &:last-child {
            &:after {
                display: none;
            }
        }
    }

    @include below-mobile-sm {
        &__anchor {
            font-size: 16px;
        }
    }
}