/******************************************************************
ALERT STYLES
Styles for alert and error components and messages
******************************************************************/


.alert-bar {
    display: none;
    margin: 0;
    padding: 24px 30px;
    background: $color-neutral;
    color: $color-white;
    animation: slideUpFadeIn 0.5s ease-out;

    &__text {
        margin: 0;
        color: $color-white;

        A {
            color: $color-white;

            &:hover {
                opacity: $opacity-md;
            }
        }
    }

    &--negative {
        background: $color-negative;
    }
    &--positive {
        background: $color-positive;
    }
    &--warning {
        background: $color-warning;
    }
    &--white {
        background: $color-white;

        .alert-bar__text {
            color: $color-text;
        } 
    }
    &--lg {
        padding: 64px 30px;
    }

    &.is-active {
        display: block;
    }
}

.alert-message {
    display: none;
    color: $color-neutral;

    &--positive {
        color: $color-positive;
    }

    &--negative {
        color: $color-negative;
    }

    &--subtle {
        color: $color-gray-dark;
        font-style: italic;
    }

    &--animated {
        animation: slideUpFadeIn 0.48s ease-in forwards;
    }

    &.is-active {
        display: inline-block;
    }
}