/******************************************************************
NAVIGATION STYLES
Styles for various navigation components used throughout the site
******************************************************************/

.module-nav {
    margin-right: -8px;

    &__item {
        display: block;
        width: 100%;
        padding-right: 8px;
        margin-bottom: 12px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    @include below-desktop {
        margin-right: 0;
        
        &__item {
            padding-right: 0;
        }
    }
}