/******************************************************************
MISC SMALL COMPONENTS
Various tiny helper components that don't belong to any category
******************************************************************/


.separator {
    display: block;
    margin: 40px 0;
    height: 1px;
    width: 100%;
    background: $color-gray-lighter;
    font-size: 0;

    &--small {
        display: inline-block;
        width: 80px;
    }

    @include below-mobile {
        margin: 24px 0;
    }
}

.scroll-anchor {
    position: absolute;
    top: -20px; // Set this to the desired height offset
    visibility: hidden;
    opacity: 0;

    &--center {
        top: 40vh;
    }

    @include below-desktop-xl {
        //top: -15px;
    }
    @include below-mobile {
        //top: -10px;
    }
}

.video-player {
    display: block;
    width: 100%;
    height: 100%;
    min-width: 100%;
    max-width: 100%;

    &--keep-aspect {
        position: relative;

        &:before {
            content: "";
            display: block;
            padding-top: 56.25%;
        }

        IFRAME {
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            height: 100%;
            width: 100%;
        }
    }
}