/******************************************************************
ACCORDION
Styles for accordion components
******************************************************************/


.accordion-content {
    height: 0;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    transform: translateX(-12px);
    transition: visibility $trans-time-sm linear, opacity $trans-time-sm linear, transform $trans-time-sm ease-out;

    &.is-active {
        height: auto;
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
        transition: visibility $trans-time-sm linear, opacity $trans-time-sm linear, transform $trans-time-sm ease-out;
    }
}

.accordion-bar {
    position: relative;
    display: flex;
    align-items: center;
    padding: 16px 64px 16px 24px;
    min-height: 64px;
    border: 1px solid $color-gray-light;
    transition: $transition-default;

    &__icon {
        position: absolute;
        right: 24px;
        top: 24px;
        height: 16px;
        width: 16px;
    }

    &.is-active {
        .accordion-bar__icon {
            transform: rotate(-180deg);
        }
    }

    &:hover {
        opacity: $opacity-sm;
        transition: $transition-default;
    }
}