/******************************************************************
FOOTER STYLES
Styles specific to the site footer
******************************************************************/

.footer {
    padding: 40px 0;
    background: $color-black;

    &__inner {
        display: flex;
        justify-content: space-between;
        margin: 0 -40px;
        color: $color-white;
        font-weight: $font-weight-bold;
        font-style: italic;
    }

    &__copyright, &__nav {
        padding: 0 40px;
    }

    @include below-desktop {
        &__inner {
            margin: 0;
            text-align: center;
            flex-direction: column-reverse;
        }

        &__copyright, &__nav {
            padding: 0;
        }

        &__copyright {
            margin-top: 36px;
            opacity: $opacity-md;
            font-size: $font-size-xs;
        }
    }

    @include below-mobile-sm {
        &__inner {
            font-size: 18px;
        }
    }
}

.footer-nav {
    .menu {
        display: flex;
        flex-wrap: wrap;
    }

    .menu__item {
        position: relative;
        padding: 0 24px;

        &:not(:last-child) {
            &:after {
                content: "/";
                position: absolute;
                top: 0;
                right: 0;
            }
        }
    }

    @include below-desktop {
        .menu {
            justify-content: center;
        }
    }
}