/******************************************************************
LIST STYLES
Styles for various layout lists that contain granular items
******************************************************************/


.icon-list {
    &__item {
        display: flex;
        align-items: center;
        position: relative;
        min-height: 64px;
        padding-left: 110px;

        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    &__icon-wrap {
        position: absolute;
        left: 0;
        top: 0;
        height: 64px;
        width: 64px;
        border-radius: 32px;
        background: $color01-light;
    }

    &__icon {
        position: absolute;
        left: 36px;
        top: 12px;
        width: 42px;
        height: 42px;
    }

    &__text {
        display: block;
        font-weight: $font-weight-black;
        font-style: italic;
        text-transform: uppercase;
    }

    @include below-mobile {
        &__item {
            padding-left: 96px;
        }

        &__text {
            font-weight: $font-weight-bold;
            text-transform: none;
        }

        &__icon-wrap {
            height: 56px;
            width: 56px;
        }

        &__icon {
            left: 36px;
            top: 10px;
            width: 36px;
            height: 36px;
        }
    }
}

.socials-list {
    margin: 0 -10px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    &__item {
        padding: 0 10px;
        margin-bottom: 8px;
    }

    &__anchor {
        display: block;
        line-height: 1;
    }

    // MODS
    &--nowrap {
        flex-wrap: nowrap;
        
        .socials-list__item {
            margin-bottom: 0;
        }
    }
}

.blog-list {
    &__item {
        &:not(:last-child) {
            margin-bottom: 65px;
        }
    }
}

.programs-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -30px;

    &__item {
        flex: 1 0 50%;
        max-width: 50%;
        padding: 0 30px;
        margin-bottom: 60px;
    }

    @include below-desktop-lg {
        margin: 0 -20px;

        &__item {
            padding: 0 20px;
        }
    }

    @include below-desktop {
        &__item {
            flex: 1 0 100%;
            max-width: 100%;
        }
    }
}

.product-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -14px;

    &__item {
        flex: 1 0 25%;
        max-width: 25%;
        padding: 0 14px;
        margin-bottom: 80px;
    }

    &--slider {
        flex-wrap: nowrap;
        margin: 0;

        .product-list__item {
            flex: 1;
            padding: 0;
            margin: 0;
            max-width: none;
        }
    }


    @include below-desktop-lg {
        &__item {
            flex: 1 0 33.33333%;
            max-width: 33.33333%;
            margin-bottom: 80px;
        }
    }

    @include below-desktop {
        &__item {
            flex: 1 0 50%;
            max-width: 50%;
        }
    }

    @media (max-width: 600px) {
        &__item {
            flex: 1 0 100%;
            max-width: 100%;

            /*&:last-child {
                margin-bottom: 0;
            }*/
        }
    }
}

.label-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -8px;
    margin-right: -8px;

    &__item {
        padding: 0 8px;
        margin-bottom: 8px;
    }
}

.attribute-list {
    &__item {
        display: block;

        &:not(:last-child) {
            padding-bottom: 20px;
            border-bottom: 1px solid $color-gray-lighter;
            margin-bottom: 24px;
        }
    }

    &__item-inner {
        display: flex;
        justify-content: space-between;
        margin: 0 -6px;
    }

    &__item-title, &__item-value {
        padding: 0 6px;
        margin-bottom: 6px;
        font-style: italic;
    }

    &__item-value {
        font-weight: $font-weight-black;
    }

    &__item--gear {
        &:not(:last-child) {
            padding-bottom: 14px;
        }
    }
}

.inline-list {
    display: inline-flex;
    flex-wrap: wrap;
    margin: 0 -8px;

    &__item {
        padding: 0 8px;
        margin-bottom: 8px;
        line-height: 1;
    }
}

.button-list {
    display: flex;
    align-items: center;
    margin: 0 -8px;

    &__item {
        padding: 0 8px;
        margin-bottom: 16px;
    }

    &--center {
        justify-content: center;
    }
    &--space-between {
        justify-content: space-between;
    }
}