/******************************************************************
PAGINATION STYLES
Styles for pagination components
******************************************************************/

.pagination {
    &__items {
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
        margin: 0 -12px;
        font-weight: $font-weight-black;
        font-style: italic;
        line-height: 1;
    }

    &__item {
        padding: 0 12px;
    }

    &__anchor {
        display: inline-block;

        &.is-active {
            color: $color-hover;
        }
    }

    &__icon {
        height: 22px;
        width: 22px;
    }
}