/******************************************************************
TABS STYLES
Styling for nav tabs
******************************************************************/


.nav-tabs {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 -20px;

    &__item {
        flex: 1;
        max-width: 320px;
        padding: 0 20px;
        margin-bottom: 8px;
    }

    &__anchor {
        display: block;
        width: 100%;
        padding: 8px;
        text-align: center;
        font-size: $font-size-h4;
        font-weight: $font-weight-bold;
        text-transform: uppercase;
        font-style: italic;
        white-space: nowrap;
        border-bottom: 2px solid rgba($color-text, 0);
        opacity: $opacity-lg;
        transition: $transition-default;

        &:hover {
            opacity: $opacity-md;
        }

        &:hover, &.is-active {
            color: $color-text;
            opacity: 1;
            transition: $transition-default;
        }

        &.is-active {
            border-bottom: 2px solid rgba($color-text, 1);
        }
    }

    &__mobile-title {
        display: none;
    }

    &--margin-bottom-lg {
        .nav-tabs__item {
            margin-bottom: 16px;
        }
    }

    @include below-mobile {
        &__mobile-title {
            display: block;
        }

        &__anchor {
            padding: 4px 0;
        }
    }
}

.nav-tabs-content {
    position: relative;
}

.tab-content {
    position: absolute;
    left: 0; top: 0; right: 0;
    height: 0;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-80px);
    transition: visibility $trans-time-sm $trans-type, opacity $trans-time-sm $trans-type, transform $trans-time-md $trans-type;

    &.is-active {
        position: relative;
        height: auto;
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
        transition: visibility $trans-time-md $trans-type, opacity $trans-time-md $trans-type, transform $trans-time-md $trans-type;
    }
}